
import { defineComponent } from 'vue';
import FolderIcon from '@/assets/FolderIcon.vue';
import BrowserIcon from '@/assets/BrowserIcon.vue';
import MusicIcon from '@/assets/MusicIcon.vue';
import FileIcon from '@/assets/FileIcon.vue';

export default defineComponent({
  emits: ['selected', 'deselected', 'folderClick'],
  components: {
    FolderIcon,
    BrowserIcon,
    MusicIcon,
    FileIcon,
  },
  props: {
    id: {
      type: Number,
      required: true,
    },
    type: {
      type: String,
      default: null,
    },
    name: {
      type: String,
      default: '',
    },
    selected: {
      type: Boolean,
      default: false,
    },
    content: {
      type: Array,
      default: () => [],
    },
  },
  methods: {
    select() {
      if (!this.selected) {
        this.$emit('selected', this.id);
      } else {
        this.$emit('deselected', this.id);
      }
    },
    dblclick() {
      if (this.type === 'folder') {
        this.$emit('folderClick', {
          id: this.id,
          name: this.name,
          content: this.content,
        });
        return;
      }
      const application = {
        name: this.name,
        type: this.type,
      };
      this.$store.dispatch('openApplication', application);
    },
  },
});
